.header-wrapper {z-index: 2;//border: 1px solid greenyellow;
  max-width: $wrapper-maxw;
  width: 100%;//для ie (иначе будет не по центру)
  height: 14.4rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto; 
  background-image: linear-gradient(to top, transparent, rgba($color: #2c3e50, $alpha: .5));

  &.no-scroll--pos{right: 16px;}
}
.header {//border: 1px solid greenyellow;
  margin-left: auto;
  margin-right: auto;
  max-width: $container-maxw;
  padding-left: 20px;
  padding-right: 5px;
  display: flex;
  justify-content: space-between;
  
  &__menu-top {
    flex-basis: 500px;
  }
}
.logo {//border: 1px solid black;
  display: flex;
  align-items: flex-end;
  
  &__link {//border: 1px solid greenyellow;
      font-family: $font-family-logo;
      font-size: 5rem;
      color: $color-default;
  }
}
.menu-top {//border: 1px solid black;

  &__list {//border: 1px solid greenyellow;
    display: flex;
    justify-content: space-between;
  }

  &__item {//border: 1px solid blueviolet;
    list-style: none;
    flex-basis: 100px;
    height: 7.2rem;
    border-radius: 0 0 6px 6px;
    transition: background-color .1s ease;
  }

  &__link {//border: 1px solid burlywood;
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: flex-end;
    padding-bottom: 1.6rem;
  }

  &__link-text {//border: 1px solid black;
    font-size: 1.8rem;
    color: $color-default;
  }
}
