.team-cards-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.team-card{
  transition: all .25s linear;
  &__name, &__position, &__photo-wrapper{
    transition: all .25s linear;
  }
  &__social-item{
    transition: all .25s linear;
  }
}
.team-card {//border: 1px solid black;
  width: 260px;
  min-height: 400px;
  margin-bottom: 20px;
  padding-top: 50px;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 40px;
  background-color: rgba($color: #4d3f4b, $alpha: .5);
  border-radius: 6px;
  overflow: hidden;

  &__photo-wrapper {//border: 1px solid black;
    width: 155px;
    height: 155px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 35px;
    border: 5px solid #a1859e;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
  }

  &__photo, &__photo-active {//border: 1px solid black;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    // background-repeat: no-repeat;
    // background-position: center;
    position: absolute;
    top: -2px;
    left: -2px;
    backface-visibility: hidden;
    transition: transform 1s ease;
  }

  &__photo{transform: rotateY(0);}
  &__photo-active{transform: rotateY(180deg);}

  &__name {display: block;
    text-transform: uppercase;
    color: #c7b6c5;
    text-align: center;
    padding-bottom: 13px;
    font-size: 3rem;
    font-weight: 900;
  }
  &__position {
    display: block;
    text-align: center;
    font-size: 1.4rem;
    color: #a1ca6c;
    line-height: 2rem;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    margin-bottom: 35px;
  }
  &__social {
    width: 180px;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    justify-content: space-between;

    &-item {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: #a1859e;
      background-repeat: no-repeat;
      background-position: center;
    }
    &-facebook {
      background-image: url(../img/facebook_8x15.png);
    }
    &-twitter {
      background-image: url(../img/twit_15x12.png);
    }
    &-google {
      background-image: url(../img/google_15x16.png);
    }
    &-linkedin {
      background-image: url(../img/linkedin_14x14.png);
    }
  }
}
.team-card:hover {
  background-color: rgba($color: #4d3f4b, $alpha: .8);
}
.team-card:hover :nth-child(1) {border-color: #df5c64;}
.team-card:hover :nth-child(1) :nth-child(1) {
  transform: rotateY(-180deg);
}
.team-card:hover :nth-child(1) :nth-child(2) {
  transform: rotateY(0);
}
.team-card:hover :nth-child(2) {color: #fff;}
.team-card:hover :nth-child(3) {color: #ff8973;}
.team-card:hover :nth-child(4) :nth-child(1) {
  background-color: #4f689e;
}
.team-card:hover :nth-child(4) :nth-child(2) {
  background-color: #74c7d5;
}
.team-card:hover :nth-child(4) :nth-child(3) {
  background-color: #df5c64;
}
.team-card:hover :nth-child(4) :nth-child(4) {
  background-color: #3e61af;
}

@mixin team-cards-media-1160{
  .team-cards-wrapper{
    max-width: 540px;
    margin-left: auto;
    margin-right: auto;
  }
}
@mixin team-cards-media-580{
  .team-cards-wrapper{
    max-width: 260px;
    margin-left: auto;
    margin-right: auto;
  }
}