
.slick-slider-home {//border: 1px solid mediumaquamarine;
  position: absolute;
  left: 50%;
  top: 50vh;
  transform: translate(-50%, -50%);
  max-width: calc(100% - 100px);
  min-height: 170px;
  padding-left: 330px;
  padding-right: 330px;
  visibility: hidden;
  
  &.slick-initialized { visibility: visible; }
}
.card-slogan {//border: 1px solid blueviolet;
  max-width: 840px;
  min-height: 170px;
  position: relative;
  border-radius: 4px;
  background-color: rgba($color: #fff, $alpha: .3);

  &__inner{
    height: 170px;//для ie
    display: flex;
    justify-content: center;
    align-items: center;
    color: #74c7d5;
  }
  &__arsis, &__thesis{//border: 1px solid black;
    min-height: 100px; 
  }
  &__arsis{
    flex-basis: 400px;
    padding-right: 16px;
  }
  &__arsis-title, &__thesis{
    text-transform: uppercase;
  }
  &__arsis-title, &__arsis-subtitle{
    font-size: 52px;
    font-weight: 300;
    text-align: right;
  }
  &__arsis-subtitle{
    text-transform: lowercase;
  }
  &__thesis{
    flex-basis: 440px;
    font-size: 124px;
    font-weight: 900;
    text-align: left;
  }
}

.slick{

  &-arrow{
    font-size: 0;
    width: 34px;
    height: 56px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  &-prev{
    left: 0;
    background: url(../img/arrow_left.svg) 0 0 / 100% no-repeat;
  }
  &-next{
    right: 0;
    background: url(../img/arrow_right.svg) 0 0 / 100% no-repeat;
  }

  &-dots{
    position: relative;

    & li{
      width: 20px;
      height: 20px;
      position: absolute;
      top: 33vh;
      border-radius: 50%;
      cursor: pointer;
      background-color: rgba($color: #fff, $alpha: .5);

      &:nth-child(1){left:calc(50% - 70px);}
      &:nth-child(2){left:calc(50% - 10px);}
      &:nth-child(3){left:calc(50% + 50px);}
    }
    & .slick-active{
      background-color: rgba($color: #74c7d5, $alpha: 1);
    }
    & button{
      font-size: 0;
    }
  }
}

@mixin slick-slider-home-media-1600{
  .slick-slider-home{
    padding-left: calc(100% - 78vw);
    padding-right: calc(100% - 78vw);
  }
}
@mixin slick-slider-home-media-1460{
  .slick-slider-home{
    padding-left: calc(100% - 82vw);
    padding-right: calc(100% - 82vw);
  }
  .slick-dots li{top: 30vh;}
}
@mixin slick-slider-home-media-1386{
  .slick-slider-home{
    padding-left: calc(100% - 84vw);
    padding-right: calc(100% - 84vw);
  }
}
@mixin slick-slider-home-media-1220{
  .slick-slider-home{
    min-height: 165px;
    padding-left: calc(100% - 88vw);
    padding-right: calc(100% - 88vw);
  }
  .card-slogan{
    min-height: 165px;
    &__inner{height: 165px;}//для ie
    &__arsis-title, &__arsis-subtitle{font-size: 50px;}
    &__thesis{font-size: 119px;}
  }
}
@mixin slick-slider-home-media-1060{
  .slick-slider-home{
    min-height: 157px;
    padding-left: calc(100% - 92vw);
    padding-right: calc(100% - 92vw);
  }
  .card-slogan{
    min-height: 157px;
    &__inner{height: 157px;}//для ie
    &__arsis-title, &__arsis-subtitle{font-size: 48px;}
    &__thesis{font-size: 114px;}
  }
  .slick-dots li{top: 29vh;}
}

