.services-content {
  margin-left: 20px;
  margin-right: 20px;
  padding-bottom: 12rem;

  &__head, &__title, &__text {
    text-align: center;
  }
  &__head {
    text-transform: uppercase;
    font-size: 6rem;
    font-weight: 300;
    margin-bottom: 4.6rem;
  }
  &__title {
    text-transform: uppercase;
    font-size: 2rem;
    line-height: 3.4rem;
    margin-bottom: 2.2rem;

    &-hl {
      color: #ec9da2;
    }
  }
  &__text {
    font-size: 1.4rem;
    line-height: 2.4rem;
    color: #ffcccf;
  }
}