.about-content {
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 6.5rem;

  &__head, &__text, &__meet-team {
    text-align: center;
  }
  &__head {
    text-transform: uppercase;
    font-size: 6rem;
    font-weight: 300;
    margin-bottom: 3.5rem;
  }
  &__text {
    font-size: 1.4rem;
    line-height: 2.4rem;
    color: #e6d1e3;
    margin-bottom: 6rem;
  }
  &__meet-team {
    width: 28rem;
    height: 5.8rem;
    margin-left: auto;
    margin-right: auto;
    background-color: #a1ca6c;
    text-transform: uppercase;
    font-size: 2.5rem;
    line-height: 5.8rem;
    font-weight: 900;
    border-radius: 6px;
    //margin-bottom: 1.5rem;

    &-triangle-down {
      width: 0;
      height: 0;
      margin-left: auto;
      margin-right: auto;
      //triangle-down
      border-left: 1.6rem solid transparent;
      border-right: 1.6rem solid transparent;
      border-top: 1.6rem solid #a1ca6c;
    }
  }
}