.services-cards-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.service-card {//border: 1px solid black;
  width: 260px;
  min-height: 400px;
  margin-bottom: 20px;
  padding-top: 50px;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 40px;
  // padding-left: 30px;
  // padding-right: 30px;
  background-color: rgba($color: #c8535a, $alpha: .8);
  border-radius: 6px;
  overflow: hidden;

  &__img-wrapper {
    width: 130px;
    height: 130px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 35px;
    border: 5px solid #fff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__img {
    width: 110px;
    height: 110px;
    background-color: #fff;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
  }
  &__title {display: block;
    text-transform: uppercase;
    // margin-left: auto;
    // margin-right: auto;
    text-align: center;
    padding-bottom: 50px;
    font-size: 3rem;
    color: $color-default;
    font-weight: 300;

  }
  &__text {
    // margin-left: auto;
    // margin-right: auto;
    text-align: center;
    font-size: 1.4rem;
    line-height: 2rem;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
  }
}

@mixin services-cards-media-1160{
  .services-cards-wrapper{
    max-width: 540px;
    margin-left: auto;
    margin-right: auto;
  }
}
@mixin services-cards-media-580{
  .services-cards-wrapper{
    max-width: 260px;
    margin-left: auto;
    margin-right: auto;
  }
}