.contacts-content{
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 12rem;

  &__head, &__text{
    text-align: center;
  }
  &__head{
    text-transform: uppercase;
    font-size: 60px;
    font-weight: 300;
    margin-bottom: 3.5rem;
  }
  &__text{
    font-size: 1.4rem;
    line-height: 2.4rem;
    color: $color-default;
    margin-bottom: 6rem;
  }
}