.contacts-inner{
  max-width: 1120px;
  margin-left: -10px;
  margin-right: -10px;
  display: flex;
  justify-content: space-between;
}

// .contacts__address, 
// .contacts__feedback{
// }

.address__title{
  font-size: 3rem;
  font-weight: 300;
  margin-bottom: 3rem;
}
.address__inner{
  display: flex;
}
.address__col{
  width: 200px;
  margin-right: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.address__company{
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 700;
  color: #c0dfda;
}
.address__location{ line-height: 1.2; }
.address__col-com{ line-height: 1.35; }

.address__phone-title,
.address__email-title,
.address__skype-title{ 
  margin-right: 1rem; 
  color: #c0dfda;
}
.address__phone-link,
.address__email-link,
.address__skype-link{ 
  color: #fff; 
}

.map{background-color: #333;
  width: 260px;
  height: 220px;
  border-radius: .6rem;
  overflow: hidden;
}
.map__frame{
  width: 100%;
  height: 100%;
}

.feedback__title{
  font-size: 3rem;
  font-weight: 300;
  margin-bottom: 3rem;
}
.user-data-wrapper{
  display: flex;
  justify-content: center;
}
.user-data{
  width: calc(50% - 10px);
  height: 4rem;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-bottom: 2rem;
  border-radius: 0.6rem;
  line-height: 2;
  color: #82c8bd;
  background-color: #68a097;
}
.user-name{
  margin-right: 20px;
}
.feedback__item{
  display: block;
  border-radius: .6rem;
}
.user-text{
  resize: none;
  width: 540px;
  height: 160px;
  padding: .5rem 1rem;
  line-height: 1.2;
  color: #82c8bd;
  background-color: #68a097;
  margin-bottom: 3rem;
}
.btn-send{
  width: 260px;
  height: 60px;
  margin-left: auto;
  margin-right: auto;
  font-style: 26px;
  font-weight: 900;
  text-transform: uppercase;
  color: #fff;
  background-color: #df5c64;
}

@mixin contacts-inner-1060{
  .contacts-inner{
    margin-left: auto;
    margin-right: auto;
  }
  .contacts__feedback{
    width: calc(100% - 10px);
    padding-left: 10px;
    margin-left: auto;
    margin-right: auto;
  }
  .user-data-wrapper{ flex-direction: column; }
  .user-data, .user-text{
    margin-right: 0;
    width: calc(100% - 10px);;//calc(100vw - 550px);
  }
}
@mixin contacts-inner-860{
  .contacts-inner{ justify-content: center; }
  
  .contacts__address, 
  .contacts__feedback{ flex-basis: 50%; }
  
  // .address__title{
  //   text-align: center;
  //   transform: translateX(-6rem);
  // }
  .address__inner{
    flex-direction: column;
    //align-items: center;
  }
  .address__col{ margin-bottom: 1.5rem; }
  .address__col-head{ margin-bottom: 4rem; }
  //.map{ transform: translateX(20px); }
  .user-data, .user-text{ width: 100%; }
  .user-text{ height: 207px; }
}