.portfolio-nav{
  width: 100%;
  height: 10rem;
  padding-left: 2rem;
  padding-right: 2rem;
  border-top: 1px solid #94b963;
  border-bottom: 1px solid #94b963;
  margin-bottom: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.portfolio-nav__title{
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 700;
}

.portfolio-menu{

  &__list{
  height: 3.5rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  }
  
  &__item + &__item{margin-left: 3.2rem;}

  &__link{
    color: $color-default;
    font-size: 1.6rem;
    font-weight: 700;
    display: block;
    padding: .6em .6em;
    border-radius: .2em;
    transition: background-color .2s ease;

    &:hover{background-color: #f5c149;}
  }
}
.active-category{background-color: #f5c149;}