#portfolio-card-desc {
	background: rgba($color: #607a55, $alpha: .5);
	padding: 20px 30px;
	width: 1100px;
  //height: 520px;
  position: relative;
  margin: 40px auto;
  //top: 50%;
  transform: translateX(-8px);
  overflow: hidden;
  border-radius: 4px;
}

.portfolio-card-desc{//border: 1px solid red;
  width: 100%;
  height: 600px;
  display: flex;
  &__img{//border: 1px solid red;
    min-width: 680px;
    height: 100%;
    border-radius: 4px;
  }
  &__desc{//border: 1px solid red;
    //padding-top: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__desc{
    padding: 0 16px 0 32px;
  }
  &__title{
    margin: 1.5rem auto;
    font-size: 2.5rem;
    line-height: 1.35;
    text-align: center;
  }
  &__text{
    padding-top: 1rem;
    padding-bottom: 1rem;
    overflow: auto;
    font-size: 16px;
    line-height: 1.2;
    border-top: 1px solid rgba($color: #fff, $alpha: .5);
    border-bottom: 1px solid rgba($color: #fff, $alpha: .5);

    &-p{
      margin: 1rem auto;
      text-indent: 1rem;
    }
  }
  &__link{
    display: block;
    font-size: 2rem;
    margin: 2rem auto 0;
    padding: 1rem;
    border-radius: .5rem;
    cursor: pointer;
    transition: background-color .3s ease;

    &:hover{
      background-color: rgba($color: #607a55, $alpha: 1);
    }
  }
}


/**
 * Fade-zoom animation for first dialog
 */
/* start state */
.my-mfp-zoom-in .zoom-anim-dialog {
	opacity: 0;
  transition: all 0.2s ease-in-out; 
  transform: scale(0.8); 
}
/* animate in */
.my-mfp-zoom-in.mfp-ready .zoom-anim-dialog {
	opacity: 1;
  transform: scale(1); 
}
/* animate out */
.my-mfp-zoom-in.mfp-removing .zoom-anim-dialog {
	transform: scale(0.8); 
  opacity: 0;
}
/* Dark overlay, start state */
.my-mfp-zoom-in.mfp-bg {
	opacity: 0;
	transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-zoom-in.mfp-ready.mfp-bg {
	opacity: 0.8;
}
/* animate out */
.my-mfp-zoom-in.mfp-removing.mfp-bg {
	opacity: 0;
}
/**
 * Fade-move animation for second dialog
 */
/* at start */
.my-mfp-slide-bottom .zoom-anim-dialog {
	opacity: 0;
	transition: all 0.2s ease-out;
  transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
}
/* animate in */
.my-mfp-slide-bottom.mfp-ready .zoom-anim-dialog {
	opacity: 1;
	transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
}
/* animate out */
.my-mfp-slide-bottom.mfp-removing .zoom-anim-dialog {
	opacity: 0;
  transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
}
/* Dark overlay, start state */
.my-mfp-slide-bottom.mfp-bg {
	opacity: 0;
  transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-slide-bottom.mfp-ready.mfp-bg {
	opacity: 0.8;
}
/* animate out */
.my-mfp-slide-bottom.mfp-removing.mfp-bg {
	opacity: 0;
}