.portfolio-cards-wrapper{
  width: 1120px;
  margin-left: -10px;
  margin-right: -10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.hide-card{display: none;}

.portfolio-card{//border: 1px solid black;
  width: 280px;
  min-height: 250px;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 20px;

  &__link{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 260px;
    border-radius: 6px;
    overflow: hidden;
  }
  &__img-wrapper{//border: 1px solid black;
    width: 100%;
    height: 200px;
    position: relative;
    background-color: #3d4d29;
    
    &::before, &::after{
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      transition: opacity .6s ease;

      .portfolio-card__link:hover &{ opacity: 1; }
    }
    &::before{
      content: '';
      top: 50%;
      width: 50px;
      height: 50px;
      border: 2px solid #fff;
      border-radius: 50%;
    }
    &::after{
      content: '+';
      top: calc(50% - 2px);
      font-size: 50px;
      font-weight: 300;
      color: #fff;
    }
  }
  &__img{ 
    transition: opacity .6s ease; 

    .portfolio-card__link:hover &{ opacity: 0; }
  }
  &__desc{ //border: 1px solid rgb(80, 28, 201);
    flex-basis: 5rem; // работает как min-height из-зи column
    width: 100%;
    flex-grow: 1;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  &__triangle-up{
    position: absolute;
    top: calc(-1.6rem + 1px);
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 0;
    //triangle-up
    border-left: 1.6rem solid transparent;
    border-right: 1.6rem solid transparent;
    border-bottom: 1.6rem solid #fff;
  }
  &__title{//border: 1px solid rgb(80, 28, 201);
    padding: 1.4rem 0.7rem;
    line-height: 1.5;
    text-align: center;
    font-size: 1.4rem;
    text-transform: uppercase;
    color: #727272;
  }
}

@mixin portfolio-cards-1160{
  .portfolio-cards-wrapper{
    max-width: 860px;
    margin-left: auto;
    margin-right: auto;
  }
}
@mixin portfolio-cards-860{
  .portfolio-cards-wrapper{
    max-width: 580px;
    margin-left: auto;
    margin-right: auto;
  }
}
@mixin portfolio-cards-580{
  .portfolio-cards-wrapper{
    max-width: 280px;
    margin-left: auto;
    margin-right: auto;
  }
}