//----------fonts---------
@import "../fonts/_fonts";

//---------modules--------
  //slick-slider version 1.8.1
@import "../modules/slick-slider/_slick-1.8.1";
  //magnific-popup
@import "../modules/magnific-popup/_magnific-popup";

//-------styles-zero------
@import "../common/_styles-zero";
//-----------vars----------
@import "../common/_vars";

//-----------blocks--------
body{//background-color: navajowhite;
  font-size: $font-size-default;
  font-family: $font-family-default;
  color: $color-default;
  overflow-x: hidden;
  
  &.no-scroll{ overflow: hidden; }
  &.no-scroll--pr{ padding-right: 16px; }
}  
.wrapper{//border: 1px solid red;
  max-width: $wrapper-maxw;
  position: relative;
}
.wrapper, .fullscreen{min-height: 100vh;}
.wrapper, .container{
  margin-left: auto;
  margin-right: auto;
}
.fullscreen{
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.fullscreen:not(#home){
  padding-top: 200px;
  padding-bottom: 120px;
}
.container{//border: 1px solid limegreen;
  max-width: $container-maxw;
  padding: 0 $container-p-lr;
}

@import "../blocks/_header";

@import "../blocks/_slick-slider-home";

@import "../blocks/_services-content";
@import "../blocks/_services-cards";

@import "../blocks/_about-content";
@import "../blocks/_team-cards";

@import "../blocks/_work-content";
@import "../blocks/_portfolio-nav";
@import "../blocks/_portfolio-cards";
@import "../blocks/_portfolio-card-desc";

@import "../blocks/_contacts-content";
@import "../blocks/_contacts-inner";

//-----------------------------media-----------------------------
@media (max-width: 1600px){
  @include slick-slider-home-media-1600;
}
@media (max-width: 1460px){
  @include slick-slider-home-media-1460;
}
@media (max-width: 1386px){
  @include slick-slider-home-media-1386;
}
@media (max-width: 1220px){
  @include slick-slider-home-media-1220;
}
@media (max-width: 1160px){
  @include services-cards-media-1160;
  @include team-cards-media-1160;
  @include portfolio-cards-1160;
}
@media (max-width: 1060px){
  @include slick-slider-home-media-1060;
  @include contacts-inner-1060;
}
@media (max-width: 860px){
  @include portfolio-cards-860;
  @include contacts-inner-860;
}
@media (max-width: 580px){
  @include services-cards-media-580;
  @include team-cards-media-580;
  @include portfolio-cards-580;
}